<template>
  <div id="app">
    <div class="card">
      <h1>Simple Weather App</h1>
      <div class="box">
        <input type="text" class="underline-input" placeholder="Search one city" v-model="search" />
        <el-button type="primary" round @click="getData" style="margin-left: 10px">Search</el-button>
        <el-button type="primary" round @click="updateData" style="margin-left: 10px">Historical query</el-button>
        <el-button type="primary" round @click="resetData" style="margin-left: 10px">Clear</el-button>
      </div>
      <div class="dataBox">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>Urban weather conditions</span>
          </div>
          <div class="dataTable">
            city:
            <p>{{ cityName }}</p>
            weather:
            <p>{{ weather }}</p>
            Today's temperature:
            <p>{{ temperature }}</p>
            Current temperature:
            <p>{{ Ctemperature.toFixed(0) + '°C' }}</p>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'App',
  data() {
    return {
      search: '',
      cityName: '',
      weather: '',
      temperature: 0,
      Ctemperature: 0
    }
  },
  methods: {
    getData() {
      let apiKey = '4d8fb5b93d4af21d66a2948710284366'
      if (this.search !== '') {
        axios
          .get(`https://api.openweathermap.org/data/2.5/weather?q=${this.search}&appid=${apiKey}&units=metric`)
          .then(res => {
            console.log(res.data)
            if (res.data.cod === 200) {
              this.cityName = res.data.name
              this.weather = res.data.weather[0].description
              let TempMax = res.data.main.temp_max
              let TempMin = res.data.main.temp_min
              this.temperature = `${TempMin.toFixed(0)}°C ~ ${TempMax.toFixed(0)}°C`
              this.Ctemperature = res.data.main.feels_like
              localStorage.setItem('search', this.search)
            }
          })
          .catch(err => {
            console.log(err)
            alert('查询失败,未查询到信息')
          })
      } else {
        this.search = ''
        this.cityName = ''
        this.weather = ''
        this.temperature = 0
        this.Ctemperature = 0
        return
      }
    },
    updateData() {
      this.search = localStorage.getItem('search')
      this.getData()
    },
    resetData() {
      this.search = ''
      this.getData()
    }
  }
}
</script>

